import React, {useState, useEffect} from 'react';
import {Panel, PanelHeader, Group, View, Div,Headline, Cell,
        ActionSheet, ActionSheetItem, Button, Link} from "@vkontakte/vkui";
import Task from './Task';
import Icon56CheckCircleOutline from '@vkontakte/icons/dist/56/check_circle_outline';
import axios from 'axios';
import config from './../config';
import bridge from '@vkontakte/vk-bridge';

let Shop = ({id}) => {
    //{"app_id": 7416969, }
    useEffect((event) => {
        console.log('NEW EVENT = ', event)
    })
    let handle = () => {
        bridge.send("VKWebAppOpenPayForm", {"app_id": 7416969, "action": "pay-to-user", "params": {"amount": 1, "user_id": 477503899}});
    }
    return (
        <View id={id} activePanel={id}>
			<Panel id={id}>
				<PanelHeader>Уведомления123</PanelHeader>
                <Cell onClick={ () => handle()}>
                    КУПИТЬ
                </Cell>
			</Panel>
		</View>
    );
}

export default Shop;